// Module
___CSS_LOADER_EXPORT___.push([module.id, `.nav-bar {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    padding: 0 25px;
    margin-right: 2vw;
    margin-top: 7.5vh;
    width: 12.5vw;
}
.nav-btn {
    border: none;
    font-size: 1rem;
    text-align: left;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
    background-color: white;
    padding: 5px 10px;
    transition: background-color 300ms;
}
.nav-btn:hover {
    background-color: var(--background-color-hover);
    transition: background-color 300ms;
    border-radius: 6px;
}
.nav-icon {
    width: 1.8rem;
    height: 1.8rem;
}
@media only screen and (max-width:600px)  { 
    #sidebar-menu-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 12.5vw;
        border-radius: 100px;
        padding: 10px 10px;
    }
    .nav-bar {
        display: none;
        overflow: hidden;
    }
}
@media only screen and (min-width:601px)  { 
    /* tablet, landscape iPad, lo-res laptops ands desktops */ 
    .nav-bar {
        width: 20vw;
        margin-top: 10vh;
        margin-left: 10px;
        padding-left: 0;
        padding-right: 0;
    }
    .nav-bar p {
        font-size: 1.3rem;
    }
}
@media only screen and (min-width:962px) { 
    /* big landscape tablets, laptops, and desktops */ 
    .nav-bar {
        width: 20vw;
        margin-top: 10vh;
        margin-left: 10px;
        padding-left: 0;
        padding-right: 0;
    }
    .nav-bar p {
        font-size: 1.7rem;
    }
}
@media (min-width:1281px) {
     /* hi-res laptops and desktops */
    .nav-bar {
        margin-top: 12.5vh;
    }
    .nav-btn {
        font-size: 1.4rem;
    }
}
